<template>
  <v-app>
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs:2 sm:18 md4>
            <v-card class="elevation-19">
              <v-toolbar dark color="primary lighten-1">
                <v-toolbar-title>Añadir usuario </v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <form>
                  <v-select v-model="tipopersona" :items="items" label="Tipo de Documento"></v-select>
                  <v-text-field v-model="documento" label="Numero de Documento" required></v-text-field>
                  <!-- <v-select v-model="selecionadoRol" :items="rolPersona" dense filled rounded label="Rol">
                  </v-select> -->
                  <v-autocomplete v-bind="buscarRol()" v-model="selecionadoRol" :items="rolPersona" label="Rol" persistent-hint
                    prepend-icon="mdi-shield-account-outline">
                  </v-autocomplete>
                  <v-text-field v-model="nombre" label="Nombre" required></v-text-field>
                  <v-text-field v-model="apellidos" label="Apellidos" required></v-text-field>
                  <v-text-field v-model="direccion" label="Direccion" required></v-text-field>
                  <v-autocomplete v-bind="buscarid()" v-model="ciudad" :items="states" label="Ciudad" persistent-hint
                    prepend-icon="mdi-city">
                  </v-autocomplete>
                  

                  <v-text-field v-model="contacto" label="Contacto" required></v-text-field>
                  <v-text-field v-model="telefono" label="Telefono" required></v-text-field>
                  <v-text-field v-model="celular" label="Celular" required></v-text-field>
                  <v-text-field v-model="cargo" label="Cargo" required></v-text-field>
                  <v-text-field v-model="correo" label="Correo" required></v-text-field>

                  <v-text-field v-model="password" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'" name="input-10-1" label="Password" hint="At least 8 characters"
                    counter @click:append="show1 = !show1"></v-text-field>

                  <v-btn class="mr-4" @click="Registrar()">registrar</v-btn>

                  <v-btn color="primary" @click="inicio()">
                    ir al inicio
                  </v-btn>
                </form>
              </v-card-text>
            </v-card>

            <v-alert :value="alert" type="success" transition="scale-transition">
              {{ mensajep }}
            </v-alert>
            <v-alert :value="alert2" type="red darken-4" transition="scale-transition">
              {{ mensajen }}
            </v-alert>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import axios from "axios";
export default {
  name: "PageRegistrar",
  dialog: false,

  data: () => ({
    correo: "",
    nombre: "",
    apellidos: "",
    selecionadoRol: "",
    documento: "",
    direccion: "",
    ciudad: "",
    contacto: "",
    telefono: "",
    celuar: "",
    cargo: "",
    nomciudad: "",
    idciudad: "",
    mensajep: "",
    mensajen: "",
    items: ["TI", "PASAPORTE", "NIT", "CC"],
    alert: false,
    alert2: false,
    value: "",
    show1: false,
    code: [],
    model: null,
    states: [],
    rr: "",
    ojo: "",
    ci: "",
    rolPersona: [
      { text: "CLIENTE", value: "Cliente" },
      // { text: "ADMIN", value: "ADMIN" },
      { text: "TECNICO", value: "Tecnico" },
      { text: "SUPERVISOR", value: "Supervisor" },
      // { text: "CIENTIFICO", value: "CIENTIFICO" },
      { text: "RECEPCIONISTA", value: "Recepcionista" },
    ],
  }),
  created() {
    this.ciudadesGet();
  },

  methods: {
    changeState(valor) {
      this.selecionadoRol = this.rolPersona[valor - 1];
      this.selecionadoTipo = this.tipoPersona[valor - 1];
      this.seleccionadoCiudad = this.Municipio[valor - 1];
    },
    buscarRol(id) {
      console.log(id);
      this.id = id
    },

    ciudadesGet() {
      const eResultado = [];

      axios
        .get("http://localhost:4000/proyecto/ciudad/CiudadDepartamento")

        .then((response) => {

          this.code = response.data.ciudad;
          console.log(this.code);

          for (let i = 0; i < response.data.ciudad.length; i++) {
            this.city = response.data.ciudad[i].Ciudad;


            eResultado.push(this.city);
          }

        })
        .catch((error) => {
          console.log(error);
        });
      this.states = eResultado;

    },

    buscarid() {
      axios
        .get(
          `http://localhost:4000/proyecto/ciudad/${this.ciudad}`

        )
        .then((response) => {
          console.log(response.data);

          for (let i = 0; i < response.data.length; i++) {
            this.ci = response.data[i].Ciudad;

            if (this.ci === this.ciudad) {
              this.idciudad = response.data[i]._id;
            }
          }
          console.log(this.idciudad);
        })
        .catch((error) => {
          console.log(error);
        });


    },
    Registrar() {
      axios
        .post("http://localhost:4000/proyecto/usuario/insertarUsuario", {
          correo: this.correo,
          password: this.password,
          nombre: this.nombre,
          rol: this.selecionadoRol,
          apellidos: this.apellidos,
          documento: this.documento,
          direccion: this.direccion,
          ciudad: this.idciudad,
          contacto: this.contacto,
          telefono: this.telefono,
          celular: this.celular,
          cargo: this.cargo,
        })
        .then((response) => {
          this.mensajep = response.data.msg;
          this.alert = true;
          this.alert2 = false;

        })
        .catch((error) => {
          console.log(error);
          this.mensajen = error;
          this.alert2 = true;
          this.alert = false;
        });
    },
    inicio() {
      this.$router.push("/Home");
    },
  },
};
</script>

<style>
#app {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

#content {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #ffffff;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.canvas-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
</style>