<script>
import axios from "axios";
export default {
  name: "PageHome",
  data() {
    return {
      token: this.$store.state.token,
      datos: this.$store.state.datos,
      peliculas: [],
      rating: 4.5,
    };
  },
  created() {
    this.peliculasGet();
  },
  computed: {
    busqueda() {
      console.log(this.$store.state.busqueda);
      return this.peliculas.filter((e) => {
        return (
          e.titulo.includes(this.$store.state.busqueda.toLowerCase()) ||
          e.subtitulo.includes(this.$store.state.busqueda.toLowerCase())
        );
      });
    },
  },
  methods: {
    listarCotizacionesGet() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios.get("http://localhost:4000/proyecto/cotizacion/cotizacion", header)
        .then((response) => {
          console.log(response);
          this.cotizacion = response.data.cotizacion;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    ir1(p) {
      this.$store.commit("setCotizacion", p);
      console.log(this.$store.state.cotizacion);
      this.$router.replace("/Cotizacion");
    },
    ir5(p) {
      this.$store.commit("setPrueba", p);
      console.log(this.$store.state.cotizacion);
      this.$router.replace("/Prueba");
    },
    ir3(p) {
      this.$store.commit("setRegistrarse", p);
      console.log(this.$store.state.cotizacion);
      this.$router.replace("/Registrarse");
    },
    Volver() {
      this.$router.push("/");
    },
  },
};
</script>
