        },
        { text: "Creacion", value: "createdAt" },
        { text: "Estado", value: "estado" },
        { text: "Observaciones", value: "observaciones" },
        { text: "Total", value: "total" },
        { text: "Actions", value: "actions", sortable: false },
      ],
      headers2: [
        {
          text: "Nombre",
          align: "start",
          sortable: false,
          value: "nombre",
        },
        { text: "Identificacion", value: "documento" },
        { text: "Direccion", value: "direccion" },
        { text: "opciones", value: "opciones" },
      ],
    };
  },

  methods: {
    Seleccionar(cliente) {
      console.log(cliente);
      if (cliente.contacto) {
        this.idCliente = cliente.id;
        this.idcontacto = cliente.contacto.id;
        this.tipoPersona = cliente.tipoPersona;
        this.nombre = cliente.nombre;
        this.apellidos = cliente.apellidos;
        this.contacto = cliente.contacto;
        this.nombreContacto = cliente.contacto.nombre;
        this.documento = cliente.documento;
        this.direccion = cliente.direccion;
        this.ciudad = cliente.ciudad;
        this.direccionTomaMuestra = cliente.direccion;
        this.telefono = cliente.telefono;
        this.celular = cliente.celular;
        this.procedimientoMuestreo = cliente.procedimientoMuestreo;
        this.cargo = cliente.cargo;
        this.correo = cliente.correo;
        this.dialog3 = false;
        this.botones = 0;
      } else {
        this.idCliente = cliente.id;
        this.tipoPersona = cliente.tipoPersona;
        this.nombre = cliente.nombre;
        this.apellidos = cliente.apellidos;
        this.contacto = "";
        this.nombreContacto = "";
        this.documento = cliente.documento;
        this.direccion = cliente.direccion;
        this.ciudad = cliente.ciudad;
        this.direccionTomaMuestra = cliente.direccion;
        this.telefono = cliente.telefono;
        this.celular = cliente.celular;
        this.cargo = cliente.cargo;
        this.correo = cliente.correo;
        this.dialog3 = false;
        this.botones = 0;
      }
    },
    Datos0() {
      this.dialog2 = !this.dialog2;

      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        // .get("http://localhost:4000/proyecto/usuario/b", header)
        .get("http://localhost:4000/proyecto/muestra/buscar", header)
        .then((res) => {
          console.log(res.data.muestra);
          this.clientes = res.data.muestra;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    created() {
      this.listarCotizaciones();
      this.muestraGetBuscar();
