<template>


   <v-content class="img">
      <v-parallax height="900" width="800"
         src="https://c.wallhere.com/photos/c6/5c/chemistry_science_laboratories-1697571.jpg!d">
      
      <v-container fluid fill-height>




         <v-layout align-center justify-center>
            <v-flex xs12 sm8 md4>
               <v-card class="elevation-12">
                  <v-toolbar dark color="cyan darken-4">
                     <v-toolbar-title>Login </v-toolbar-title>
                  </v-toolbar>

                  <v-card-text>
                     <v-form>
                        <v-text-field v-model="correo" label="Correo" type="text"></v-text-field>
                        <v-text-field v-model="password" label="Password" type="password"></v-text-field>

                     </v-form>
                  </v-card-text>

                  <v-card-actions>
                     <v-spacer></v-spacer>
                     <v-btn color="cyan accent-4" @click="login()">Login</v-btn>
                     <!-- <v-btn color="cyan accent-4" @click=Registrarse()>registro</v-btn> -->
                  </v-card-actions>
               </v-card>
            </v-flex>
         </v-layout>


         
      </v-container>
</v-parallax>
   </v-content>


</template>


<script>
import axios from "axios"
export default {
   name: 'PageLogin',

   data: () => ({
      correo: "",
      password: ""
   }),
   methods: {
      ver(p) {
         this.$store.commit("", {
            nombre: p.nombre,
            precio: p.precio,
            cantidad: 1,
            imagen: p.imagen,
         })
      },
      login() {
         axios.post("http://localhost:4000/proyecto/usuario/login", {
            correo: this.correo,
            password: this.password,
            // peliculas:[]

         })


            .then(response => {
               console.log(response.data.token);
               this.$store.dispatch("setToken", response.data.token)
               this.$store.dispatch("setDatos", response.data.usuario)
               //this.$router.push("/articulos")
               this.$router.push("/Home")

               // this.peliculas=response.data.usuario

            })
            .catch(error => {
               console.log(error);
            })

      },
      Registrarse() {
         this.$router.push('/Registrarse')
      }
   }
}
</script>
